import axios from 'axios';
import service from '../axios/request'
import url from "../static/js/hostUrl";
let base = url.url;

export const login = params => { return axios.post(`${base}login`, params).then(res => res.data); };

export const checkPatients = params => { return axios.post(`${base}operation/check/`, params).then(res => res.data); };

export const removePatients = params => { return axios.get(`${base}/operation/remove/`, { params: params }); };

export const batchRemoveUser = params => { return axios.get(`${base}/user/batchremove`, { params: params }); };

export const editPatientBaicInfos = params => { return axios.post(`${base}operation/editBasicInfo/`, params).then(res => res.data); };

export const editPatientReviewInfos = params => { return axios.post(`${base}operation/editReviewInfo/`, params).then(res => res.data); };

export const addPatients = params => { return axios.post(`${base}operation/add/`, params).then(res => res.data); };

export const sendSmsToPatient = params => { return axios.post(`${base}operation/sendSms/`, params).then(res => res.data); };

export const checkPatient = data => {
  return service({
    url: 'operation/check/',
    method: 'post',
    data
  })
};
export const removePatient = data => {
  return service({
    url: 'operation/remove/',
    method: 'post',
    data
  })
};

export const editPatientBaicInfo = data => {
  return service({
    url: 'operation/editBasicInfo/',
    method: 'post',
    data
  })
};

export const editPatientReviewInfo = data => {
  return service({
    url: 'operation/editReviewInfo/',
    method: 'post',
    data
  })
};

export const addPatient = data => {
  return service({
    url: 'operation/add/',
    method: 'post',
    data
  })
};




