<template>
  <div id="app">
    <login v-if="$store.state.login"></login>
    <leftNav v-if="$store.state.leftNav" class="leftNav" id="leftNav"></leftNav>
    <div class="main" id="rightNav" v-if="$store.state.leftNav">
      <router-view/>
      <topHeader v-if="$store.state.topHeader"></topHeader>
      <undiagnosed v-if="$store.state.undiagnosed"></undiagnosed>
      <diagnosed v-if="$store.state.diagnosed"></diagnosed>
      <transform v-if="$store.state.transform"></transform>
      <statistics v-if="$store.state.statistics"></statistics>
      <report v-if="$store.state.report"></report>
    </div>
  </div>
</template>

<script>
  window.onresize=function () {
    let rightNavWidth = window.innerWidth-250;
    let leftNavHeight = window.innerHeight;
    document.getElementById('rightNav').style.width = rightNavWidth+'px';
    document.getElementById('leftNav').style.height = leftNavHeight+'px';
  };
import store from '@/vuex/store'
import leftNav from '@/components/common/leftNav'
import topHeader from '@/components/common/topHeader'
import undiagnosed from '@/components/common/undiagnosed'
import diagnosed from '@/components/common/diagnosed'
import transform from '@/components/common/transform'
import statistics from '@/components/common/statistics'
import report from '@/components/common/report'
import login from '@/components/common/login'
import cookie from './static/js/cookie';
export default {
  name: 'App',
  //项目启动的时候先检查是否有token,如果有则直接登陆,如果没有则返回登陆界面
  created:function () {
    let token=cookie.getCookie('token');
    if(token){
      this.$store.commit('login');
    }else{
      this.$store.commit('logout');
    }
  },
  mounted:function(){
    let rightNavWidth = window.innerWidth-250;
    console.log(rightNavWidth);
    document.getElementById('rightNav').style.width = rightNavWidth+'px';
  },
  components:{
    leftNav,
    topHeader,
    undiagnosed,
    diagnosed,
    transform,
    statistics,
    report,
    login
  },
  data(){
    return{
      seen:false,
      msg:'Hello Vuex'
    }
  },
  store
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
.main{
  float: right;
  /*width: 87%;*/
  background-color: #FFFFFF;
  /*height: 100%;*/
  overflow: hidden;
  }
  .leftNav{
    position: fixed;
  }
</style>



