import axios from 'axios'
import cookie from '../static/js/cookie'
import Vue from 'vue'
import url from "../static/js/hostUrl";
let base = url.url;
let v = new Vue();

/****** 创建axios实例 ******/
const service = axios.create({
  baseURL: base,  // IP地址 在/static/js/hostUrl中配置
  timeout: 5000  // 请求超时时间
});


/****** request拦截器==>对请求参数做处理 ******/
service.interceptors.request.use(config => {
  let user_token = cookie.getCookie('token');
  console.log('拦截中', user_token);
  if(user_token===null){
   v.$message('登陆过期,即将返回登陆页面');
    setTimeout("window.location.reload()",2000);
    return Promise.reject(error);
  }

  config.method === 'post'
    ? config.data = {...config.data}
    : config.params = {...config.params};
  config.headers['Content-Type'] = 'application/x-www-form-urlencoded';

  return config;
}, error => {  //请求错误处理
  v.$message(error);
  return Promise.reject(error);
});


service.interceptors.response.use(

  response => {  //成功请求到数据
    // Vue.$vux.loading.hide();
    console.log(response.data);
    //这里根据后端提供的数据进行对应的处理
    if (response.data) {
      return response.data;
    } else {
      return response.data;
    }
  },
  error => {  //响应错误处理
    console.log('error');
    console.log(error);
    console.log(JSON.stringify(error));
    let text = JSON.stringify(error).status === 404
      ? '404'
      : '操作异常';
    console.log(text);

    return Promise.reject(error)
  }
);

export default service



