<template>
  <div class="undiagnosed">
    <el-row>
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item :to="{ path: '/' }">PACS</el-breadcrumb-item>
        <el-breadcrumb-item>转化列表</el-breadcrumb-item>
      </el-breadcrumb>
    </el-row>

    <el-row class="operationNew">
      <el-col v-bind:class="{ active: isActive1 }" :span="2"><a @click="turnBlue" id="isActive1" href="javascript:;">全部</a></el-col>
      <el-col v-bind:class="{ active: isActive4 }" :span="3" class="timepicker"><a @click="" id="isActive4" href="javascript:;"><el-date-picker
        v-model="dateValue"
        align="right"
        type="date"
        @change="selectDate"
        placeholder="选择日期"
        :picker-options="pickerOptions">
      </el-date-picker></a></el-col>
      <el-col :span="6">&nbsp;</el-col>
      <el-col :span="4"><el-button type="primary" @click="batchSendSms">批量发送短信<i class="el-icon-s-comment el-icon--right"></i></el-button></el-col>
      <div style="text-align: center">
        <el-col :span="2" ><a href="javascript:;" v-on:click="refresh"><i class="icon iconfont icon-shuaxin1"></i>刷新</a></el-col>
        <el-col :span="5"><el-input
          v-model="inputVal"
          placeholder="输入患者姓名/手机号/身份证号 可模糊检索">
        </el-input></el-col>
        <el-col :span="2"><el-button type="primary" @click="searchBtn" icon="el-icon-search">搜索</el-button></el-col>
      </div>
    </el-row>

    <el-row class="dataZone">
      <template>
        <el-table
          ref="multipleTable"
          :data="tableData"
          stripe
          style="width: 100%"
          @selection-change="handleSelectionChange">
          <el-table-column
            type="selection"
            width="55">
          </el-table-column>
          <el-table-column
            prop="patient_id"
            label="患者编号"
            >
          </el-table-column>
          <el-table-column
            prop="patient_name"
            label="姓名"
           >
          </el-table-column>
          <el-table-column
            prop="patient_gender"
            label="性别"
            :formatter="formatSex">
          </el-table-column>
          <el-table-column
            prop="patient_age"
            label="年龄"
            >
          </el-table-column>
          <el-table-column
            prop="patient_tel"
            label="手机号"
            >
          </el-table-column>
          <el-table-column
            prop="apply_time"
            label="录入时间"
          :formatter="formatTime">
          </el-table-column>
          <el-table-column
            prop="diagnose[diagnose.length - 1].inform_type"
            label="状态"
          :formatter="formatStatus">
          </el-table-column>
          <el-table-column
            label="操作">
            <template slot-scope="scope">
              <el-button @click="showSms(scope.$index, scope.row)" type="primary" size="small" icon="el-icon-chat-dot-round" circle></el-button>
              <el-button @click="handleEdit(scope.$index, scope.row)" type="primary" size="small" icon="el-icon-edit" circle></el-button>
            </template>
          </el-table-column>
        </el-table>
      </template>
    </el-row>
    <el-row class="pagination">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page.sync="currentPage"
        :page-sizes="[5,10]"
        :page-size="page_size"
        layout="total, sizes, prev, pager, next, jumper"
        :total="totalNum">
      </el-pagination>
    </el-row>
    <!--编辑界面-->
    <el-dialog title="编辑复查医院和时间" :visible.sync="editFormVisible" v-model="editFormVisible" :close-on-click-modal="false">
      <el-form :model="editForm" label-width="80px" :rules="editFormRules" ref="editForm">
        <el-form-item label="姓名" prop="patient_name" style="width: 25%">
          <el-input v-model="editForm.patient_name" auto-complete="off" readonly=""></el-input>
        </el-form-item>
        <el-form-item label="手机号" prop="patient_tel">
          <el-input type="text" placeholder="手机号" oninput="value=value.replace(/[^\d]/g,'')" maxlength="11" show-word-limit v-model="editForm.patient_tel" style="width: 25%"></el-input>
        </el-form-item>
        <el-form-item label="复查时间" style="width: 25%" prop="review_time">
<!--          <el-input type="text" v-model="editForm.review_time"></el-input>-->
          <el-date-picker
            v-model="editForm.review_time"
            align="right"
            type="date"
            placeholder="选择日期"
            value-format="yyyy-MM-dd"
            :picker-options="reviewTimeOption">
          </el-date-picker>

        </el-form-item>
        <el-form-item label="复查医院" style="width: 25%" prop="review_hospital">
          <el-input type="text" v-model="editForm.review_hospital"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click.native="editFormVisible = false">取消</el-button>
        <el-button type="primary" @click.native="editSubmit" :loading="editLoading">提交</el-button>
      </div>
    </el-dialog>

    <!--弹框提示短信内容-->
    <el-dialog
      title="提示"
      :visible.sync="dialogVisible"
      width="30%">
      <span>您将要发送如下短信:<br/>尊敬的{{editForm.patient_name}}
        <span v-if="editForm.patient_gender==='0'">女士</span><span v-else>先生</span>,
        请于{{editForm.review_time}}前,前往{{editForm.review_hospital}}进行眼底情况检查</span>
      <span slot="footer" class="dialog-footer">
    <el-button @click="dialogVisible = false">取 消</el-button>
    <el-button type="primary" @click="sendSms">确 定</el-button>
  </span>
    </el-dialog>

  </div>
</template>

<script>
  import axios from 'axios'
  import {editPatientReviewInfo, sendSmsToPatient} from '../../api/api';
  import url from "../../static/js/hostUrl";
  import cookie from "../../static/js/cookie";
  let host = url.url;
  export default {
    name:'undiagnosed',
    created:function(){
      this.get_patients(1,10)
    },
    methods:{
      /*刷新*/
      refresh:function () {
        let p = 1;
        this.currentPage = 1;
        let page_size = this.page_size;
        let search  = this.inputVal;
        if(this.otherDate){
          this.get_patients_by_rules(p,page_size,this.dateValue,search);
          console.log("otherdate");
        }else{
          this.get_patients_by_rules(p,page_size,'',search);
          console.log("total");
        }
      },
      /*控制每页多少数量*/
      handleSizeChange( val) {
        let page_size = val;
        this.page_size = page_size;
        this.currentPage = 1;
        let p = this.currentPage;
        let search = this.inputVal;
        console.log(p,page_size);
        if(this.otherDate){
          this.get_patients_by_rules(p,page_size,this.dateValue,search);
          console.log("otherdate");
        }else{
          this.get_patients_by_rules(p,page_size,'',search);
          console.log("page_size total");
        }
      },
      /*控制第几页*/
      handleCurrentChange(val) {
        let p  = val;
        let page_size = this.page_size;
        let search = this.inputVal;
        console.log(p,page_size);
        if(this.otherDate){
          this.get_patients_by_rules(p,page_size,this.dateValue,search);
          console.log("otherdate");
        }else{
          this.get_patients_by_rules(p,page_size,'',search);
          console.log("pagination total");
        }
      },

      turnBlue:function(e){
        let thisActive = e.currentTarget.id;
        if(thisActive == "isActive1"){
          console.log('isActive1');
          this.isActive1 = true;
          this.isActive2 = false;
          this.isActive3 = false;
          this.isActive4 = false;
          this.otherDate = false;
          this.dateValue = '';
          this.refresh();
        }
      },

      /*去重*/
      tabledataProcess:function(array){
      let temp1 = []; //一个新的临时数组
      let temp2 = [];
      for(let i = 0; i < array.length; i++){
      if(temp1.indexOf(array[i].patient_id) === -1){
        let diagnoseTimes = array[i].diagnose.length;
        if(array[i].diagnose[diagnoseTimes-1].review_time!==null){
          temp1.push(array[i].patient_id);
          temp2.push(array[i]);
        };
    }
  }
      console.log(temp2);
      return temp2;
  },

      get_patients:function (p,size,apply_time,search) {
        let hospital_id = cookie.getCookie('hospital_id');
        axios.get(host+"/patients/?p="+p+"&page_size="+size+"&hospital_id="+hospital_id+"&ordering=-apply_time&review_time=-")
          .then(response=>{
            // this.tableData = response.data.results;
            // this.totalNum = response.data.count;
            let data = this.tabledataProcess(response.data.results);
            this.tableData = data;
            this.totalNum = data.length;
          })
          .catch(error=>{
            console.log((error))
          })
      },
      get_patients_by_rules:function (p,size,apply_time,search) {
        let hospital_id = cookie.getCookie('hospital_id');
        axios.get(host+"/patients/?p="+p+"&page_size="+size+
          "&ordering=-apply_time"+"&apply_time="+apply_time+"&search="+search+"&hospital_id="+hospital_id+"&review_time=-")
          .then(response=>{
            let data = this.tabledataProcess(response.data.results);
            // this.tableData = response.data.results;
            // this.totalNum = response.data.count;
            this.tableData = data;
            this.totalNum = data.length;
          })
          .catch(error=>{
            console.log((error))
          })
      },
      selectDate:function () {
        this.isActive1=false;
        this.isActive4=true;
        this.otherDate = true;
        let seperator1 = "-";
        let date = new Date(this.dateValue);
        let year = date.getFullYear();
        let month = date.getMonth() + 1;
        let strDate = date.getDate();
        if (month >= 1 && month <= 9) {
          month = "0" + month;
        }
        if (strDate >= 0 && strDate <= 9) {
          strDate = "0" + strDate;
        }
        let selectdate = year + seperator1 + month + seperator1 + strDate;
        this.dateValue  = selectdate;
        this.currentPage = 1;
        let search = this.inputVal;
        console.log(this.dateValue);
        console.log(search);
        this.get_patients_by_rules(1,this.page_size,selectdate,search);
      },
      searchBtn:function () {
        console.log('searchBtn');
        this.currentPage = 1;
        this.get_patients_by_rules(1,this.page_size,this.dateValue,this.inputVal);
      },
      //性别显示转换
      formatSex: function (row) {
        return row.patient_gender === "1" ? '男' : row.patient_gender === "0" ? '女' : '未知';
      },
      //状态显示转化
      formatStatus: function (row) {
        let lastOne = row.diagnose.length - 1;
        return row.diagnose[lastOne].inform_type === "unInform" ? '未通知' : row.diagnose[lastOne].inform_type === "message" ? '已短息通知' : '已电话通知';
      },
      //时间显示转化
      formatTime: function (row) {
        let lastOne = row.diagnose.length - 1;
        let apply_time = row.diagnose[lastOne].apply_time;
        return  apply_time
      },
      //显示编辑界面
      handleEdit: function (index, row) {
        console.log(index,row);
        let lastOne = row.diagnose.length;
        row['review_time'] = row.diagnose[lastOne-1].review_time;
        row['review_hospital'] = row.diagnose[lastOne-1].review_hospital;
        this.editFormVisible = true;
        this.editForm = Object.assign({}, row);
      },
      //编辑
      editSubmit: function () {
        this.$refs.editForm.validate((valid) => {
          if (valid) {
            this.$confirm('确认提交吗？', '提示', {}).then(() => {
              this.editLoading = true;
              //NProgress.start();
              let para = Object.assign({}, this.editForm);
              console.log(para);
              // para.birth = (!para.birth || para.birth == '') ? '' : util.formatDate.format(new Date(para.birth), 'yyyy-MM-dd');
              editPatientReviewInfo(para).then((res) => {
                this.editLoading = false;
                //NProgress.done();
                this.$message({
                  message: '提交成功',
                  type: 'success'
                });
                this.$refs['editForm'].resetFields();
                this.editFormVisible = false;
                this.refresh();
              });
            });
          }
        });
      },
      handleSelectionChange(val) {
        this.multipleSelection = val;
        console.log(val);
      },
      //显示单个短信发送页面
      showSms: function (index, row) {
        this.dialogVisible=true;
        console.log(index,row);
        let lastOne = row.diagnose.length;
        row['review_time'] = row.diagnose[lastOne-1].review_time;
        row['review_hospital'] = row.diagnose[lastOne-1].review_hospital;
        this.editForm = Object.assign({}, row);
      },
      //发送短信
      sendSms:function () {
        this.dialogVisible = false;
        let para = Object.assign({}, this.editForm);
        sendSmsToPatient(para).then((res) => {
          if(res=='1'){
            this.$message({
              message: '短信发送成功',
              type: 'success'
            });
          }else {
            this.$message({
              message: '短信发送失败',
              type: 'error'
            });
          }
          this.editLoading = false;
          //NProgress.done();
          this.refresh();
        });
      },
      //批量发送短信
      batchSendSms:function () {
        if(this.multipleSelection.length==0){
          this.$message({
            message: '请选择患者',
            type: 'error'
          });
          return
        }
        this.dialogVisible = false;
        let para = this.multipleSelection;
        sendSmsToPatient(para).then((res) => {
          console.log('res>',res);
          if(res=='1'){
            this.$message({
              message: '短信发送成功',
              type: 'success'
            });
          }else {
            this.$message({
              message: '短信发送失败',
              type: 'error'
            });
          }
          this.editLoading = false;
          //NProgress.done();
          this.refresh();
        });
      }
    },
    data() {
      return {
        tableData: [],
        multipleSelection: [],
        dateValue:'',
        isActive1:true,
        isActive2:false,
        isActive3:false,
        isActive4:false,
        currentPage: 1,
        inputVal:'',
        page_size:10,
        totalNum:30,
        otherDate:false,
        dialogVisible: false,
        pickerOptions: {
          disabledDate(time) {
            return time.getTime() > Date.now();
          },
          shortcuts: [{
            text: '今天',
            onClick(picker) {
              picker.$emit('pick', new Date());
            }
          }, {
            text: '昨天',
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24);
              picker.$emit('pick', date);
            }
          }, {
            text: '一周前',
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit('pick', date);
            }
          }]
        },

        reviewTimeOption: {
          disabledDate(time) {
            return time.getTime() < Date.now();
          },
        },

        editFormVisible: false,//编辑界面是否显示
        editLoading: false,
        editFormRules: {
          patient_name: [
            { required: true, message: '请输入姓名', trigger: 'blur' }
          ],
          patient_tel: [
            { required: true, message: '请输入手机号', trigger: 'blur' }
          ],
          review_time: [
            { required: true, message: '请输复诊时间', trigger: 'blur' }
          ],
          review_hospital: [
            { required: true, message: '请输复诊医院', trigger: 'blur' }
          ],
        },
        //编辑界面数据
        editForm: {
          patient_id: 0,
          patient_name: '测试',
          patient_gender: '0',
          patient_age: 0,
          patient_tel:18888888888,
          birth: '',
          address: '',
          review_time:'',
          review_hospital:'兴园社区'
        },

      }
    }
  }

</script>

<style scoped>
  .undiagnosed{
    text-align: left;
    margin-top: 20px;
  }
  .timezone,.dataZone{
    margin-top: 20px;
  }
  .el-date,.el-icon-date,.icon-shuaxin1{
    color: dodgerblue;
  }

  .operationNew{
    margin-top: 20px;
    text-align: center;
    line-height: 41px;
  }
  a{
    text-decoration: none;
    color: dodgerblue;
  }

  .el-input__inner{
    border: dodgerblue 1px solid;
    color: dodgerblue;
  }

  .active{
    border: 1px solid dodgerblue;
    border-radius: 4%;
  }

  /*.active>>> a{*/
  /*  color: #FFFFFF;*/
  /*  */
  /*}*/
  /*.active >>> i{*/
  /*  color: #FFFFFF;*/
  /*}*/

  .pagination{
    margin-top: 20px;
    text-align: right;
  }

  .el-date-editor.el-input, .el-date-editor.el-input__inner{
    width: 140px;
  }

  .timepicker >>> i{
    color: dodgerblue;
    font-size: x-large;
  }
  .timepicker >>> input {
    border: none;
    font-size: 15px;
  }

  .timepicker >>>input::-webkit-input-placeholder{
    color: dodgerblue;
  }

  .timepicker >>>input::-moz-placeholder{
    color: blue;
  }

</style>




