<script>
  import store from '@/vuex/store'
  import axios from 'axios'
  import url from "../../static/js/hostUrl";
  let host = url.url;
  // window.onresize=function () {
  //   /*获取div高度，减去46px是因为头部组件高度46px*/
  //   let leftNavHeight = window.innerHeight-46;
  //   document.getElementById('aside_id').style.height = leftNavHeight+'px';
  // };

  export default {
    data() {
      return {
        patient_info:{},
        restaurants: [],
        state: '',
        timeout:  null,
        suggestions:['定时随访','上级医院就诊','正常,无需随访'],
        diseases:['高血压视网膜病变','糖尿病视网膜病变','豹纹状眼底','渗出',
                  '青光眼','年龄相关性视网膜病变'],
        fits: ['fill',],
        url1: 'https://www.orbisbiotech.cn/media/xingyuan/2019080410421718855145058_Right_1_origin.png',
        url2: 'https://www.orbisbiotech.cn/media/xingyuan/2019080410421718855145058_Left_2_origin.png',
        srcList: [
          'https://fuss10.elemecdn.com/8/27/f01c15bb73e1ef3793e64e6b7bbccjpeg.jpeg',
          'https://fuss10.elemecdn.com/1/8e/aeffeb4de74e2fde4bd74fc7b4486jpeg.jpeg'
        ],
        dynamicTagsR: [], /*右眼印象*/
        dynamicTagsL: [], /*左眼印象*/
        dynamicTagsA:[],  /*建议*/
        diaStrR:'',       /*完成阅片后展示的右眼印象*/
        diaStrL:'',       /*完成阅片后展示的左眼印象*/
        diaStrA:'',       /*完成阅片后展示的建议*/
        finishSeen:false, /*完成阅片前展示的按钮*/
        beforeSeen:true,  /*完成阅片后展示的按钮*/
        inputVisibleR: false,
        inputVisibleL: false,
        inputVisibleA: false,
        leftEye:false,
        rightEye:false,
        advice:false,
        inputValueR: '',
        inputValueL: '',
        inputValueA: '',
        seen:true,
        seen2:false,
        eyeSeen:false,
        diaSeen:true,
        activeColor1:'azure',
        activeColor2:'azure',
        dialogVisible: false,
        fullscreenLoading: false,
        centerDialogVisible: false,
        pickerOptions: {
          disabledDate(time) {
            return time.getTime() < Date.now();
          },
          shortcuts: [{
            text: '今天',
            onClick(picker) {
              picker.$emit('pick', new Date());
            }
          }, {
            text: '明天',
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() + 3600 * 1000 * 24);
              picker.$emit('pick', date);
            }
          }, {
            text: '一周后',
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() + 3600 * 1000 * 24 * 7);
              picker.$emit('pick', date);
            }
          }]
        },
        value1: '',
        value2: '',
      };
    },
    methods: {
      /*关闭右眼标签*/
      handleCloseR(tag) {
        this.dynamicTagsR.splice(this.dynamicTagsR.indexOf(tag), 1);
      },
      /*显示添加的输入框*/
      showInputR() {
        this.activeColor2 = 'lightgray';
        this.inputVisibleR = true;
        this.rightEye = true;
        this.leftEye = false;
        this.$nextTick(_ => {
          this.$refs.saveTagInputR.$refs.input.focus();
        });
      },
      /*确定输入*/
      handleInputConfirmR() {

        this.activeColor2 = 'azure';
        let inputValueR = this.inputValueR;
        if (inputValueR) {
          this.dynamicTagsR.push(inputValueR);
        }
        this.inputVisibleR = false;
        this.inputValueR = '';
      },

      /*关闭左眼标签*/
      handleCloseL(tag) {
        this.dynamicTagsL.splice(this.dynamicTagsL.indexOf(tag), 1);
      },
      /*显示左眼输入框*/
      showInputL() {
        this.activeColor2 = 'lightgray';
        this.inputVisibleL = true;
        this.rightEye = false;
        this.leftEye = true;
        this.$nextTick(_ => {
          this.$refs.saveTagInputL.$refs.input.focus();
        });
      },
      /*确定输入*/
      handleInputConfirmL() {
        this.activeColor2 = 'azure';
        let inputValueL = this.inputValueL;
        if (inputValueL) {
          this.dynamicTagsL.push(inputValueL);
        }
        this.inputVisibleL = false;
        this.inputValueL = '';
      },

      /*关闭建议标签*/
      handleCloseA(tag) {
        this.dynamicTagsA.splice(this.dynamicTagsA.indexOf(tag), 1);
        this.seen = true;
        this.seen2 = false;
        this.value2='';
      },
      /*显示建议输入框*/
      showInputA() {
        this.leftEye=false;
        this.rightEye=false;
        this.activeColor1 = 'lightgray';
        this.inputVisibleA = true;
        this.advice = true;
        this.$nextTick(_ => {
          this.$refs.saveTagInputA.$refs.input.focus();
        });
      },
      /*确定输入建议*/
      handleInputConfirmA() {
        this.activeColor1='azure';
        let inputValueA = this.inputValueA;
        let dynamicTagsA = this.dynamicTagsA;
        if (inputValueA) {
          this.dynamicTagsA.push(inputValueA);
        }
        if(dynamicTagsA.length !== 0 ){
          this.seen = false;
        }
        this.inputVisibleA = false;
        this.inputValueA = '';
      },
      /*点击诊断栏处诊断自动添加为诊断的标签*/
      addDiagnose(index){
        if(this.leftEye){
          this.dynamicTagsL.push(index);
          let tem_dynamicTagsL = this.duplicateRemove(this.dynamicTagsL);  /*去重*/
          this.dynamicTagsL = tem_dynamicTagsL;
          this.handleInputConfirmL();
        }else if(this.rightEye){
          this.dynamicTagsR.push(index);
          let tem_dynamicTagsR = this.duplicateRemove(this.dynamicTagsR);  /*去重*/
          this.dynamicTagsR = tem_dynamicTagsR;
          this.handleInputConfirmR();
        }
      },
      /*点击建议栏处建议自动添加为建议的标签*/
      addAdvice(index){
        let dynamicTagsA = this.dynamicTagsA;
        if(this.advice){
          if(dynamicTagsA.length !==0){
            return
          }else{
            this.dynamicTagsA.push(index);
            this.handleInputConfirmA();
          }
          if(index==='上级医院就诊'){
            this.dialogVisible=true;
            this.seen2=true;
          }
        }
      },

      formatSex: function (row) {
        return row.patient_gender === "1" ? '男' : row.patient_gender === "0" ? '女' : '未知';
      },

      /*完成阅片*/
      finishDiagnose(){
          let diaStrR = this.dynamicTagsR.toString();  /*将右眼诊断内容由数组形式转化为字符串形式*/
          let diaStrL = this.dynamicTagsL.toString();  /*将左眼诊断内容由数组形式转化为字符串形式*/
          this.diaStrR = diaStrR;
          this.diaStrL = diaStrL;
          this.submitDiagnose();  /*提交诊断内容*/
          this.eyeSeen = true;
          this.diaSeen = false;
          this.finishSeen = true;
          this.beforeSeen = false;
      },

      /*完成阅片并展示加载中*/
      openFullScreen() {
        if(this.dynamicTagsA.length===0){
          this.open(); /*如果没有选择诊断建议，则会出现提示让选择诊断建议*/
          return
        }else{
        const loading = this.$loading({
          lock: true,
          text: 'Loading',
          spinner: 'el-icon-loading',
          background: 'rgba(0, 0, 0, 0.7)'
        });
        setTimeout(() => {
          loading.close();
          this.finishDiagnose();
        }, 500);
        }
      },

      /*完成并返回未诊断页面*/
      backToUndiagnosed:function(){
        if(this.dynamicTagsA.length===0){
          this.open(); /*如果没有选择诊断建议，则会出现提示让选择诊断建议*/
          return
        }else{
        let page_info = {};
        page_info['page_size'] = this.patient_info.page_size;
        page_info['currentPage'] = this.patient_info.currentPage;
        const loading = this.$loading({
          lock: true,
          text: 'Loading',
          spinner: 'el-icon-loading',
          background: 'rgba(0, 0, 0, 0.7)'
        });
        setTimeout(() => {
          loading.close();
          this.finishDiagnose();
          this.$store.commit('backToUndiagnosed');  /*触发store.js中的backToUndiagnosed方法*/
        }, 1000);
        }
      },

      /*仅返回未诊断页面*/
      onlyBack:function(){
        let page_info = {};
        page_info['page_size'] = this.patient_info.page_size;
        page_info['currentPage'] = this.patient_info.currentPage;
        const loading = this.$loading({
          lock: true,
          text: 'Loading',
          spinner: 'el-icon-loading',
          background: 'rgba(0, 0, 0, 0.7)'
        });
        setTimeout(() => {
          loading.close();
          this.$store.commit('backToUndiagnosed',page_info);
        }, 1000);
      },

      /*获取左侧热词*/
      getConclusion:function () {
        axios.get(host+"/conclusion/?&ordering=-times")
          .then(response=>{
            console.log((response.data));
            let conclusions = response.data;
            let conclusionList = [];
            for(let i=0;i<conclusions.length;i++){
              conclusionList.push(conclusions[i]['conclusion'])
            }
            this.diseases = conclusionList.slice(0,9);
          })
          .catch(error=>{
            console.log((error))
          })
      },
      /*获取诊断内容*/
      getDiagnoseResult(){
        let data = {};
        data['patient_id'] = this.patient_info.patient_id;
        data['diagnose_record'] = this.patient_info.diagnose_record;
        data['apply_time'] = this.patient_info.apply_time;
        data['patient_name'] = this.patient_info.patient_name;
        data['patient_gender'] = this.patient_info.patient_gender;
        data['patient_age'] = this.patient_info.patient_age;
        data['left_diagnose'] = this.diaStrL;
        data['right_diagnose'] = this.diaStrR;
        data['diagnose_advice'] = this.dynamicTagsA[0];
        if(this.value2){
          data['review_time'] = this.value2;
        }
        return data
      },
      /*提交诊断内容*/
      submitDiagnose(){
        let data = this.getDiagnoseResult();
        console.log('start submit diagnose result',data);
        axios.post(`${host}operation/submit/`, data)
          .then(res=>{
            console.log('res=>',res);
            if(res['data']===0){
              this.$alert('阅片失败', '提示', {
                confirmButtonText: '确定',
              });
            }
          }).catch(error=>{
          console.log((error));
          this.$alert('阅片错误', '提示', {
            confirmButtonText: '确定',
          });
        })
      },

      /*去重*/
      duplicateRemove:function(array){
        let temp1 = []; //一个新的临时数组
        for(let i = 0; i < array.length; i++){
          if(temp1.indexOf(array[i]) === -1){
            temp1.push(array[i]);
          }
        }
        console.log(temp1);
        return temp1;
      },

      /*生成报告*/
      createReport:function(){
        let data = this.getDiagnoseResult();
        axios.post(`${host}operation/createPdf/`, data)
          .then(res=>{
            console.log('res=>',res['data']);
            if(res['data'] === 1){
              let report1=Math.random().toString(36).substr(2);
              let temWindow=window.open('',report1,'');
              temWindow.document.title="pdf";
              let url_pdf="/media/hello.pdf";
              temWindow.location=url_pdf;
            }else{
              this.$alert('诊断报告生成失败', '提示', {
                confirmButtonText: '确定',
              });
            }
          }).catch(error=>{
          console.log((error));
          this.$alert('请允许弹窗', '提示', {
            confirmButtonText: '确定',
          });
        });
      },

      /*完成并生成诊断报告*/
      finishAndCreatePdf:function(){
        if(this.dynamicTagsA.length===0){
          this.open(); /*如果没有选择诊断建议，则会出现提示让选择诊断建议*/
          return
        }else{
        try {
          console.log('start submit');
          const loading = this.$loading({
            lock: true,
            text: 'Loading',
            spinner: 'el-icon-loading',
            background: 'rgba(0, 0, 0, 0.7)'
          });
          setTimeout(() => {
            loading.close();
            this.finishDiagnose();
          }, 500);
          this.createReport();
        }catch (e) {
          console.log(e)
        }
        }
      },

      cancelSelectDate(){
        this.dialogVisible = false;
        this.seen2=false;
        this.value2 = '';
      },

      confirmSelectDate(){
        this.dialogVisible = false;
        if(!this.value2){
          this.seen2=false;
          this.value2 = '';
        }
      },

      open() {
        this.$alert('请添加建议', '提示', {
          confirmButtonText: '确定',
        });
      },

      loadAll() {
        return [
          { "value": "南拳妈妈龙虾盖浇饭", "address": "普陀区金沙江路1699号鑫乐惠美食广场A13" }
        ];
      },
      querySearchAsync(queryString, cb) {
        let restaurants = this.restaurants;
        let results = queryString ? restaurants.filter(this.createStateFilter(queryString)) : restaurants;

        clearTimeout(this.timeout);
        this.timeout = setTimeout(() => {
          cb(results);
        }, 3000 * Math.random());
      },
      createStateFilter(queryString) {
        return (state) => {
          return (state.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0);
        };
      },
      handleSelect(item) {
        console.log(item);
      },
    },

    mounted() {
      this.restaurants = this.loadAll();
      /*获取div高度，减去46px是因为头部组件高度46px*/
      document.getElementById('aside_id').style.height= window.innerHeight - 46 + 'px';
      this.patient_info = this.$store.state.patient_info; /*获取存在store.js中的数据*/
      this.getConclusion(); /*获取热词*/
    }
  };
</script>

<style scoped>
.aside{
  width: 20%;
  color: #2F4050;
  box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1)
}
.main{
  width: 80%;
}

a{
  text-decoration: none;
  color: #2F4050;
}
  .suggestion_section,.diseases_section{
    margin-top: 20px;
  }
span{
  font-family: 微软雅黑;
  font-weight: bold;
}

.el-tag + .el-tag {
  margin-left: 10px;
}
.button-new-tag {
  margin-left: 10px;
  height: 32px;
  line-height: 30px;
  padding-top: 0;
  padding-bottom: 0;
}
.input-new-tag {
  width: 200px;
  margin-left: 10px;
  /*vertical-align: bottom;*/
}
</style>



