<template>
    <div class="undiagnosed">
      <el-row>
        <el-breadcrumb separator-class="el-icon-arrow-right">
          <el-breadcrumb-item :to="{ path: '/' }">PACS</el-breadcrumb-item>
          <el-breadcrumb-item>未诊列表</el-breadcrumb-item>
        </el-breadcrumb>
      </el-row>

      <el-row class="operationNew">
        <el-col v-bind:class="{ active: isActive1 }" :span="2"><a @click="turnBlue" id="isActive1" href="javascript:;">全部</a></el-col>
        <el-col v-bind:class="{ active: isActive4 }" :span="3" class="timepicker"><a @click="" id="isActive4" href="javascript:;"><el-date-picker
          v-model="dateValue"
          align="right"
          type="date"
          @change="selectDate"
          placeholder="选择日期"
          :picker-options="pickerOptions">
        </el-date-picker></a></el-col>
        <el-col :span="8">&nbsp;</el-col>
        <el-col :span="2"><el-button type="primary" @click="handleAdd">添加患者<i class="el-icon-upload el-icon--right"></i></el-button></el-col>
        <div style="text-align: center">
        <el-col :span="2"><a href="javascript:;" v-on:click="refresh"><i class="icon iconfont icon-shuaxin1"></i>刷新</a></el-col>
        <el-col :span="5"><el-input
          v-model="inputVal"
          placeholder="输入患者姓名/手机号/身份证号 可模糊检索">
        </el-input></el-col>
        <el-col :span="2"><el-button type="primary" @click="searchBtn" icon="el-icon-search">搜索</el-button></el-col>
        </div>
      </el-row>

      <el-row class="dataZone">
        <template>
          <el-table
            :data="tableData"
            stripe
            style="width: 100%">
            <el-table-column
              prop="patient_id"
              label="患者编号"
              >
            </el-table-column>
            <el-table-column
              prop="patient_name"
              label="姓名"
              >
            </el-table-column>
            <el-table-column
              prop="patient_gender"
              label="性别"

              :formatter="formatSex">
            </el-table-column>
            <el-table-column
              prop="patient_age"
              label="年龄"
              >
            </el-table-column>
            <el-table-column
              prop="patient_tel"
              label="手机号"
              >
            </el-table-column>
            <el-table-column
              prop="apply_time"
              label="录入时间"
              >
            </el-table-column>
            <el-table-column
              prop="status"
              label="状态"
              :formatter="formatStatus">
            </el-table-column>
            <el-table-column

              label="操作">
              <template slot-scope="scope">
                <el-button  @click="clickReport(scope.$index, scope.row)" type="primary" size="small" v-if="formatButton(scope.row)" icon="el-icon-first-aid-kit" circle></el-button>
                <el-button @click="handleDel(scope.$index, scope.row)" type="danger" size="small" icon="el-icon-delete" circle v-if="!formatButton(scope.row)"></el-button>
                <el-button @click="handleEdit(scope.$index, scope.row)" type="primary" size="small" icon="el-icon-edit" circle></el-button>
              </template>
            </el-table-column>
          </el-table>
        </template>
      </el-row>
     <el-row class="pagination">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page.sync="currentPage"
        :page-sizes="[5,10]"
        :page-size="page_size"
        layout="total, sizes, prev, pager, next, jumper"
        :total="totalNum">
      </el-pagination>
     </el-row>
      <!--新增界面-->
      <el-dialog title="新增" :visible.sync="addFormVisible" v-model="addFormVisible" :close-on-click-modal="false">
        <el-form :model="addForm" label-width="80px" :rules="addFormRules" ref="addForm">
          <el-form-item label="姓名"  prop="patient_name" style="width: 25%">
            <el-input v-model="addForm.patient_name" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label="手机号" prop="patient_tel">
            <el-input type="text" placeholder="手机号" @blur="checkIsDiagnoseAgain" oninput="value=value.replace(/[^\d]/g,'')" maxlength="11" show-word-limit v-model="addForm.patient_tel" style="width: 25%"></el-input>
          </el-form-item>
          <el-form-item label="性别">
            <el-radio-group v-model="addForm.patient_gender">
              <el-radio class="radio" :label="1" v-model="addForm.genderRadio">男</el-radio>
              <el-radio class="radio" :label="0" v-model="addForm.genderRadio">女</el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item label="年龄">
            <el-input-number v-model="addForm.patient_age" oninput="value=value.replace(/[^\d]/g,'')" :min="0" :max="200" style="width: 25%"></el-input-number>
          </el-form-item>
          <el-form-item label="既往史">
            <el-radio-group v-model="addForm.jwsExist"
            :onclick="jwsClick()">
              <el-radio class="radio" :label="1" v-model="addForm.jwsRadio">有</el-radio>
              <el-radio class="radio" :label="0" v-model="addForm.jwsRadio">无</el-radio>
            </el-radio-group>
            <br/>
            <el-form-item v-if="addForm.jwsVisible">
            <span>糖尿病:</span><el-input-number v-model="jwsForm.DM_year" controls-position="right" size="mini" :min="0" :max="100"></el-input-number>年
            &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
            <span>高血压:</span><el-input-number v-model="jwsForm.HTN_year" controls-position="right" size="mini" :min="0" :max="100"></el-input-number>年
            <br/>
            <span>高血脂:</span><el-input-number v-model="jwsForm.HPL_year" controls-position="right" size="mini" :min="0" :max="100"></el-input-number>年
            &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
            <span>&nbsp;&nbsp;&nbsp;妊娠:</span><el-input-number v-model="jwsForm.pregnant_weeks" controls-position="right" size="mini" :min="0" :max="100"></el-input-number>周
            <br/>
            <span>其他:&nbsp;&nbsp;&nbsp;</span><el-input size="mini" style="width: 410px"
            placeholder="请输入内容"
            v-model="jwsForm.other"
            clearable>
          </el-input>
            <br/>
            <span>屈光度:</span>
            左眼:<template>
            <el-select v-model="leftDiopterValue" size="mini" clearable  placeholder="请选择" style="width: 150px">
              <el-option
                v-for="item in leftDiopter"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </template>
            &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;右眼:<template>
            <el-select v-model="rightDiopterValue" size="mini" clearable  placeholder="请选择" style="width: 150px">
              <el-option
                v-for="item in rightDiopter"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </template>
          </el-form-item>
          </el-form-item>
            <br/>
          <el-form-item label="地址">
            <el-input type="textarea" v-model="addForm.address"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click.native="addFormVisible = false">取消</el-button>
          <el-button type="primary" @click.native="addSubmit" :loading="addLoading">提交</el-button>
        </div>
      </el-dialog>
        <!--编辑界面-->
        <el-dialog title="编辑" :visible.sync="editFormVisible" v-model="editFormVisible" :close-on-click-modal="false">
          <el-form :model="editForm" label-width="80px" :rules="editFormRules" ref="editForm">
            <el-form-item label="姓名" prop="patient_name" style="width: 25%">
              <el-input v-model="editForm.patient_name" auto-complete="off"></el-input>
            </el-form-item>
            <el-form-item label="手机号" prop="patient_tel">
              <el-input type="text" placeholder="手机号" oninput="value=value.replace(/[^\d]/g,'')" maxlength="11" show-word-limit v-model="editForm.patient_tel" style="width: 25%"></el-input>
            </el-form-item>
            <el-form-item label="性别">
              <el-radio-group v-model="editForm.patient_gender">
                <el-radio class="radio" :label="'1'">男</el-radio>
                <el-radio class="radio" :label="'0'">女</el-radio>
              </el-radio-group>
            </el-form-item>
            <el-form-item label="年龄">
              <el-input-number v-model="editForm.patient_age" oninput="value=value.replace(/[^\d]/g,'')" :min="0" :max="200" style="width: 25%"></el-input-number>
            </el-form-item>
            <el-form-item label="既往史">
              <template>
                <el-radio-group v-model="radio" :onchange="editjws()">
                  <el-radio :label="3">有</el-radio>
                  <el-radio :label="6">无</el-radio>
                </el-radio-group>
              </template>

              <br/>
              <el-form-item v-if="editFormjwsVisible">
                <span>糖尿病:</span><el-input-number v-model="editFormJwsForm.DM_year" controls-position="right" size="mini" :min="0" :max="100"></el-input-number>年
                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                <span>高血压:</span><el-input-number v-model="editFormJwsForm.HTN_year" controls-position="right" size="mini" :min="0" :max="100"></el-input-number>年
                <br/>
                <span>高血脂:</span><el-input-number v-model="editFormJwsForm.HPL_year" controls-position="right" size="mini" :min="0" :max="100"></el-input-number>年
                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                <span>&nbsp;&nbsp;&nbsp;妊娠:</span><el-input-number v-model="editFormJwsForm.pregnant_weeks" controls-position="right" size="mini" :min="0" :max="100"></el-input-number>周
                <br/>
                <span>其他:&nbsp;&nbsp;&nbsp;</span><el-input size="mini" style="width: 410px"
                                                            placeholder="请输入内容"
                                                            v-model="editFormJwsForm.other"
                                                            clearable>
              </el-input>
                <br/>
                <span>屈光度:</span>
                左眼:<template>
                <el-select v-model="editFormLeftDiopterValue" size="mini" clearable  placeholder="请选择" style="width: 150px">
                  <el-option
                    v-for="item in editFormLeftDiopter"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                  </el-option>
                </el-select>
              </template>
                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;右眼:<template>
                <el-select v-model="editFormRightDiopterValue" size="mini" clearable  placeholder="请选择" style="width: 150px">
                  <el-option
                    v-for="item in editFormLeftDiopter"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                  </el-option>
                </el-select>
              </template>
              </el-form-item>
            </el-form-item>
            <br/>
            <el-form-item label="地址">
              <el-input type="textarea" v-model="editForm.address"></el-input>
            </el-form-item>
          </el-form>
          <div slot="footer" class="dialog-footer">
            <el-button @click.native="editFormVisible = false">取消</el-button>
            <el-button type="primary" @click.native="editSubmit" :loading="editLoading">提交</el-button>
          </div>
        </el-dialog>
    </div>
</template>

<script>
  import store from '@/vuex/store'
  import axios from 'axios'
  import {removePatient, checkPatient, editPatientBaicInfo, addPatient } from '../../api/api';
  import url from "../../static/js/hostUrl";
  import cookie from "../../static/js/cookie";
  let host = url.url;
  export default {
    name:'undiagnosed',
    created:function(){
      console.log('host',host)
      let page_info = this.$store.state.page_info;
      if(page_info.page_size&&page_info.currentPage){
        console.log('111122',page_info.page_size,page_info.currentPage);
        this.page_size = page_info.page_size;
        this.currentPage = page_info.currentPage;
        this.get_patients(page_info.currentPage,page_info.page_size);
      }else{
        console.log('222211',cookie.getCookie('hospital_id'));
        this.get_patients(1,10);
      }
    },
    mounted:function(){
      let rightNavWidth = window.innerWidth-250;
      console.log(rightNavWidth);
      document.getElementById('rightNav').style.width = rightNavWidth+'px';
    },
    methods:{
      /*刷新*/
      refresh:function () {
        let p = 1;
        this.currentPage = 1;
        let page_size = this.page_size;
        let search  = this.inputVal;
        if(this.otherDate){
          this.get_patients_by_rules(p,page_size,this.dateValue,search);
          console.log("otherdate");
        }else{
          this.get_patients_by_rules(p,page_size,'',search);
          console.log("total");
        }
      },
      /*向store.js传递page和size*/
      delieverPageAndSize:function(){
        let currentPage = this.currentPage;
        let page_size = this.page_size;
        let page_info = {};
        page_info['currentPage'] = currentPage;
        page_info['page_size'] = page_size;
        this.$store.commit('rememberPageAndSize',page_info)
      },

      /*控制每页多少数量*/
      handleSizeChange( val) {
        let page_size = val;
        this.page_size = page_size;
        this.currentPage = 1;
        let p = this.currentPage;
        let search = this.inputVal;
        this.delieverPageAndSize();
        console.log(p,page_size);
        if(this.otherDate){
          this.get_patients_by_rules(p,page_size,this.dateValue,search);
          console.log("otherdate");
        }else{
          this.get_patients_by_rules(p,page_size,'',search);
          console.log("page_size total");
        }
      },
      /*控制第几页*/
      handleCurrentChange(val) {
        let p  = val;
        let page_size = this.page_size;
        let search = this.inputVal;
        console.log(p,page_size);
        this.delieverPageAndSize();
        if(this.otherDate){
          this.get_patients_by_rules(p,page_size,this.dateValue,search);
          console.log("otherdate");
        }else{
          this.get_patients_by_rules(p,page_size,'',search);
          console.log("pagination total");
        }
      },

      turnBlue:function(e){
        let thisActive = e.currentTarget.id;
        if(thisActive == "isActive1"){
          console.log('isActive1');
          this.isActive1 = true;
          this.isActive4 = false;
          this.otherDate = false;
          this.dateValue = '';
          this.refresh();
        }
      },

      get_patients:function (p,size,apply_time,search) {
        let hospital_id = cookie.getCookie('hospital_id');
        axios.get(host+"/patients/?p="+p+"&page_size="+size+"&hospital_id="+hospital_id+"&ordering=-apply_time&status=0")
          .then(response=>{
            console.log((response))
            this.tableData = response.data.results
            this.totalNum = response.data.count
          })
          .catch(error=>{
            console.log((error))
          })
      },
      get_patients_by_rules:function (p,size,apply_time,search) {
        let hospital_id = cookie.getCookie('hospital_id');
        axios.get(host+"/patients/?p="+p+"&page_size="+size+
          "&ordering=-apply_time"+"&apply_time="+apply_time+"&hospital_id="+hospital_id+"&search="+search+"&status=0")
          .then(response=>{
            console.log((response))
            this.tableData = response.data.results
            this.totalNum = response.data.count
          })
          .catch(error=>{
            console.log((error))
          })
      },
      selectDate:function () {
        this.isActive1=false;
        this.isActive4=true;
        this.otherDate = true;
        let seperator1 = "-";
        let date = new Date(this.dateValue);
        let year = date.getFullYear();
        let month = date.getMonth() + 1;
        let strDate = date.getDate();
        if (month >= 1 && month <= 9) {
          month = "0" + month;
        }
        if (strDate >= 0 && strDate <= 9) {
          strDate = "0" + strDate;
        }
        let selectdate = year + seperator1 + month + seperator1 + strDate;
        this.dateValue  = selectdate;
        this.currentPage = 1;
        let search = this.inputVal;
        console.log(this.dateValue);
        console.log(search);
        this.get_patients_by_rules(1,this.page_size,selectdate,search);
      },
      searchBtn:function () {
        console.log('searchBtn');
        this.currentPage = 1;
        this.get_patients_by_rules(1,this.page_size,this.dateValue,this.inputVal);
      },
      //性别显示转换
      formatSex: function (row) {
        return row.patient_gender === "1" ? '男' : row.patient_gender === "0" ? '女' : '未知';
      },
      //状态显示转换
      formatStatus: function (row) {
        return row.status === "0" ? '未诊断' : row.status === "2" ? '已诊断' : '诊断中';
      },
      //诊断或删除按钮转换
      formatButton:function(row){
        return row.pic_upload === "1" ? true : row.pic_upload !== "0";
      },
      //显示编辑界面
      handleEdit: function (index, row) {
        console.log('qqqqq',index,row);
        let historyDisease = row['historyDisease'];
        console.log(historyDisease);
        if(historyDisease[0]){
          console.log('有既往史');
          this.editFormVisible = true;
          this.editForm = Object.assign({}, row);
          this.editFormJwsForm = historyDisease[0];
          this.editFormLeftDiopterValue = historyDisease[0]['left_diopter'];
          this.editFormRightDiopterValue=historyDisease[0]['right_diopter'];
          this.editForm.jwsExist=1;  //展示既往史具体内容
          this.editFormjwsVisible=true; //展示编辑界面
          this.radio=3; //点亮‘有’字
        }else{
          console.log('无既往史');
          this.editFormVisible = true;
          this.editForm = Object.assign({}, row);
          this.editForm.jwsExist=0; //不展示既往史具体内容
          this.editFormjwsVisible=false; //不展示编辑界面
          this.radio=6;//点亮‘无’字
        }
      },
      //显示新增界面
      handleAdd: function () {
        console.log('this.addForm.jwsVisible',this.addForm.jwsVisible);
        this.addFormVisible = true;
        this.addForm = {
          patient_name: '',
          patient_gender: 0,
          patient_age: 0,
          patient_tel: '',
          address: '',
          jwsRadio:0,
          genderRadio:0,
          jwsVisible:false,
          jwsExist:0
        };
      },
      //检查是否是再次就诊
      checkIsDiagnoseAgain:function(){
        let para = Object.assign({}, this.addForm);
        checkPatient(para).then((res) => {
          console.log(1111,res);
          if(res=="0"){
            console.log("查无此人")
          }else{
            console.log('resres',res);
            this.$alert('经系统检测，该名患者已存在，确认历史信息无误后可直接提交', '温馨提示', {
              confirmButtonText: '确定',
              callback: action => {
                console.log(res);
                this.addFormVisible = false;
                this.editFormVisible = true;
                this.editForm=res['fields'];
                if(res['his_fields']){
                  this.editFormJwsForm=res['his_fields'];
                  this.editFormLeftDiopterValue = res['his_fields']['left_diopter'];
                  this.editFormRightDiopterValue = res['his_fields']['right_diopter'];
                  this.editForm.jwsExist=1;
                  this.editFormjwsVisible=true;
                  this.radio=3;
                }else{
                  this.radio=6; //radio=6时，执行editjws函数就会就会关闭既往史区域，并且既往史radio框选中无
                  this.editjws();
                }
                this.editForm['patient_id'] = res['patient_id'];
                this.editForm['diagnose_again'] = '1';
                this.$refs['addForm'].resetFields();
                // this.$message({
                //   type: 'info',
                //   // message: `action: ${ action }`
                // });
              }
            });

            // this.addFormVisible = false;
            // this.refresh();
          }
        });
      },

      //提交
      addSubmit: function () {
        this.$refs.addForm.validate((valid) => {
          if (valid) {
            this.$confirm('确认提交吗？', '提示', {}).then(() => {
              this.addLoading = true;
              //NProgress.start();
              console.log('this.jwsForm',this.jwsForm);
              console.log(this.leftDiopterValue,this.rightDiopterValue);
              let jws= this.jwsForm;
              let submitInfo = {};
              if(this.leftDiopterValue!==''){
                jws['left_diopter'] = this.leftDiopterValue;
              }
              if(this.rightDiopterValue!==''){
                jws['right_diopter'] = this.rightDiopterValue;
              }
              submitInfo['jwsInfo'] = jws;
              submitInfo['basicInfo'] = this.addForm;

              console.log('submitInfo',submitInfo);
              let para = Object.assign({}, submitInfo);
              para['basicInfo']['user_token'] = cookie.getCookie('token');
              // para.birth = (!para.birth || para.birth == '') ? '' : util.formatDate.format(new Date(para.birth), 'yyyy-MM-dd');
              console.log('hhhhh',para); //此处可以拿到json格式的para
              addPatient(para).then((res) => {
                this.addLoading = false;
                //NProgress.done();
                this.$message({
                  message: '提交成功',
                  type: 'success'
                });
                this.$refs['addForm'].resetFields();
                this.addFormVisible = false;
                this.refresh();
              });
            });
          }
        });
      },
      //删除
      handleDel: function (index, row) {
        this.$confirm('确认删除该记录吗?', '提示', {
          type: 'warning'
        }).then(() => {
          // this.listLoading = true;
          //NProgress.start();
          let para = { id: row.patient_id };
          removePatient(para).then((res) => {
            // this.listLoading = false;
            //NProgress.done();
            console.log(1111,res);
            if(res === 0 ){
              this.$alert('经系统检测，该名患者已有诊断记录，无法删除', '温馨提示', {
                confirmButtonText: '确定',
                callback: action => {
                    console.log(action)
                }
              });
            }else{
              this.$message({
                message: '删除成功',
                type: 'success'
              });
              this.refresh();
            }
          });
        }).catch(() => {

        });
      },

      //点击既往史单选按钮
      jwsClick:function(){
        let jws = this.addForm.jwsExist;
        if(jws===0){
          this.addForm.jwsVisible = false;
          this.jwsForm={};
          this.leftDiopterValue='';
          this.rightDiopterValue='';
        }else if(jws===1){
          this.addForm.jwsVisible = true;
        }
      },
      editjws:function(){
        let radio = this.radio;
        console.log(radio);
        if(radio===3){
          this.editForm.jwsExist=1;
          this.editFormjwsVisible=true;
        }else if(radio===6){
          this.editFormjwsVisible=false;
          this.editFormJwsForm={};
          this.editFormRightDiopterValue='';
          this.editFormLeftDiopterValue='';
          this.editForm.jwsExist=0;
        }
      },

      //提交编辑
      editSubmit: function () {
        this.$refs.editForm.validate((valid) => {
          if (valid) {
            this.$confirm('确认提交吗？', '提示', {}).then(() => {
              this.editLoading = true;
              //NProgress.start();
              console.log(123456789);
              let jws = this.editFormJwsForm;
              console.log(this.editForm);
              let editInfo = {};
              if(this.editFormLeftDiopterValue!==''){
                jws['left_diopter'] = this.editFormLeftDiopterValue;
              }
              if(this.editFormRightDiopterValue!==''){
                jws['right_diopter'] = this.editFormRightDiopterValue;
              }
              editInfo['jwsInfo'] = jws;
              editInfo['basicInfo'] = this.editForm;

              console.log('13256',editInfo);
              let para = Object.assign({}, editInfo);
              console.log('para',para);
              // para.birth = (!para.birth || para.birth == '') ? '' : util.formatDate.format(new Date(para.birth), 'yyyy-MM-dd');
              editPatientBaicInfo(para).then((res) => {
                this.editLoading = false;
                //NProgress.done();
                this.$message({
                  message: '提交成功',
                  type: 'success'
                });
                this.$refs['editForm'].resetFields();
                this.editFormVisible = false;
                this.refresh();
              });
            });
          }
        });
      },

      clickReport:function (index,row) {
        this.editForm = Object.assign({}, row);
        let patient_info = {};
        patient_info['page_size'] = this.page_size;
        patient_info['currentPage'] = this.currentPage;
        patient_info['patient_name'] = this.editForm.patient_name;
        patient_info['patient_gender'] = this.editForm.patient_gender;
        patient_info['patient_age'] = this.editForm.patient_age;
        patient_info['patient_id'] = this.editForm.patient_id;
        patient_info['diagnose_record'] = this.editForm.diagnose_record;
        patient_info['apply_time'] = this.editForm.apply_time;
        this.$store.commit('clickReport',patient_info);
      }
    },
    data() {
      return {
        page_info:{},
        tableData: [],
        dateValue:'',
        isActive1:true,
        isActive4:false,
        currentPage: 1,
        inputVal:'',
        input:'',
        page_size:10,
        totalNum:30,
        otherDate:false,
        pic_upload:false,
        editFormjwsVisible:false,
        radio:3,
        pickerOptions: {
          disabledDate(time) {
            return time.getTime() > Date.now();
          },
          shortcuts: [{
            text: '今天',
            onClick(picker) {
              picker.$emit('pick', new Date());
            }
          }, {
            text: '昨天',
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24);
              picker.$emit('pick', date);
            }
          }, {
            text: '一周前',
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit('pick', date);
            }
          }]
        },
        editFormVisible: false,//编辑界面是否显示
        editLoading: false,
        editFormRules: {
          patient_name: [
            { required: true, message: '请输入姓名', trigger: 'blur' }
          ],
          patient_tel: [
            { required: true, message: '请输入手机号', trigger: 'blur' }
          ]
        },
        //编辑界面数据
        editForm: {
          patient_id: 0,
          patient_name: '测试',
          patient_gender: '0',
          patient_age: 0,
          patient_tel:18888888888,
          birth: '',
          jwsRadio:0,
          jwsExist:0,
          jwsVisible:false,
          address: '',
        },
        addFormVisible: false,//新增界面是否显示
        addLoading: false,
        addFormRules: {
          patient_name: [
            { required: true, message: '请输入姓名', trigger: 'blur' }
          ],
          patient_tel: [
            { required: true, message: '请输入手机号', trigger: 'blur' }
          ]
        },
        //新增界面数据
        addForm: {
          patient_id: 0,
          patient_name: '张三',
          patient_gender: 1,
          patient_age: 0,
          patient_tel: '',
          address: '',
          jwsExist:0,
          jwsVisible:false, //既往史界面是否显示
          jwsRadio:0,
          genderRadio:0,
        },
        jwsForm:{
          other:'',
          DM_year:'', //糖尿病
          HTN_year:'', //高血压
          HPL_year:'', //高血脂
          pregnant_weeks:'',
          },
        editFormJwsForm:{
          other:'',
          DM_year:'', //糖尿病
          HTN_year:'', //高血压
          HPL_year:'', //高血脂
          pregnant_weeks:'',
        },

        leftDiopter: [{
          value: '0',
          label: '正视'
        },{
          value: '1',
          label: '低度近视(75~300)'
        }, {
          value: '2',
          label: '中度近视(75~300)'
        }, {
          value: '3',
          label: '高度近视(75~300)'
        }, {
          value: '4',
          label: '远视'
        }],
        rightDiopter: [{
          value: '0',
          label: '正视'
        }, {
          value: '1',
          label: '低度近视(75~300)'
        }, {
          value: '2',
          label: '中度近视(75~300)'
        }, {
          value: '3',
          label: '高度近视(75~300)'
        }, {
          value: '4',
          label: '远视'
        }],
        editFormLeftDiopter: [{
          value: '0',
          label: '正视'
        },{
          value: '1',
          label: '低度近视(75~300)'
        }, {
          value: '2',
          label: '中度近视(75~300)'
        }, {
          value: '3',
          label: '高度近视(75~300)'
        }, {
          value: '4',
          label: '远视'
        }],
        editFormRightDiopter: [{
          value: '0',
          label: '正视'
        }, {
          value: '1',
          label: '低度近视(75~300)'
        }, {
          value: '2',
          label: '中度近视(75~300)'
        }, {
          value: '3',
          label: '高度近视(75~300)'
        }, {
          value: '4',
          label: '远视'
        }],
        leftDiopterValue: '',
        rightDiopterValue: '',
        editFormLeftDiopterValue: '',
        editFormRightDiopterValue: '',
      }
    }
  }

</script>

<style scoped>
.undiagnosed{
  text-align: left;
  margin-top: 20px;
}
  .timezone,.dataZone{
    margin-top: 20px;
  }
  .el-date,.el-icon-date,.icon-shuaxin1{
    color: dodgerblue;
  }

.operationNew{
  margin-top: 20px;
  text-align: center;
  line-height: 41px;
}
  a{
    text-decoration: none;
    color: dodgerblue;
  }

  .el-input__inner{
    border: dodgerblue 1px solid;
    color: dodgerblue;
  }

  .active{
    border: 1px solid dodgerblue;
    border-radius: 4%;
  }

  /*.active>>> a{*/
  /*  color: #FFFFFF;*/
