    text-align: right;
  }

.el-date-editor.el-input, .el-date-editor.el-input__inner{
  width: 140px;
}

.timepicker >>> i{
  color: dodgerblue;
  font-size: x-large;
}
  .timepicker >>> input {
    border: none;
    font-size: 15px;
  }

  .timepicker >>>input::-webkit-input-placeholder{
    color: dodgerblue;
  }

.timepicker >>>input::-moz-placeholder{
