<template>
    <div class="topHeader">
      <template>
        <div>
          <el-row>
            <el-col :span="21">&nbsp;</el-col>
            <el-col :span="3">
              <a href="javascript:;"><i class="el-icon-s-custom"></i>{{user_info.name}}</a>
              &nbsp;&nbsp;&nbsp;&nbsp;
              <a href="javascript:;" @click="logout"><i class="el-icon-switch-button">退出</i></a>
            </el-col>
          </el-row>
        </div>
      </template>
    </div>
</template>

<script>
  import store from '@/vuex/store'
    export default {
      name: "topHeader",
      created:function(){
        let user_info = this.$store.state.user_info;
        this.user_info = user_info;
        console.log('user_info',user_info);
      },
      methods:{
        logout:function () {
          this.$store.commit('logout');
        }

      },
      data() {
        return {
          user_info:{}
        }
      }
    }
</script>

<style scoped>
.topHeader{
  height: 46px;
  line-height: 46px;
  text-align:center;
  border-bottom: lightgrey 1px solid;
}
  a{
    text-decoration: none;
    color: #2c3e50;
  }
</style>



