import Vue from 'vue'
import Vuex from 'vuex'
import cookie from '../static/js/cookie';

Vue.use(Vuex);

const state={
  patient_info:{},
  page_info:{},
  user_info:{},
  undiagnosed:false,
  diagnosed:false,
  transform:false,
  statistics:false,
  report:false,
  leftNav:false,
  topHeader:false,
  login:true,
  hospital_id:0
};

const mutations={
  clickUndiagnosed(state){
    state.undiagnosed = true;
    state.diagnosed = false;
    state.transform = false;
    state.statistics = false;
    state.report = false;
  },
  clickDiagnosed(state){
    state.undiagnosed = false;
    state.diagnosed = true;
    state.transform = false;
    state.statistics = false;
    state.report = false;
  },
  clickStatistics(state){
    state.undiagnosed = false;
    state.diagnosed = false;
    state.transform = false;
    state.report = false;
    state.statistics = true;
  },
  clickTransform(state){
    state.undiagnosed = false;
    state.diagnosed = false;
    state.transform = true;
    state.statistics = false;
    state.report = false;
  },

  clickReport(state,patient_info){
    state.undiagnosed = false;
    state.diagnosed = false;
    state.transform = false;
    state.statistics = false;
    state.report = true;
    state.patient_info = patient_info;
  },

  backToUndiagnosed(state){
    state.undiagnosed = true;
    state.diagnosed = false;
    state.transform = false;
    state.statistics = false;
    state.report = false;
  },

  rememberPageAndSize(state,page_info){
    state.page_info = page_info;
  },

  setInfo(state){
    state.user_info = {
      name:cookie.getCookie('name'),
      token:cookie.getCookie('token')
    };
  },
  saveHospitalId(state,hospital_id){
    state.hospital_id = hospital_id;
    console.log('state.hospital',hospital_id)
  },

  login(state){
    state.undiagnosed = true;
    state.diagnosed = false;
    state.transform = false;
    state.statistics = false;
    state.report = false;
    state.leftNav = true;
    state.topHeader=true;
    state.login=false;
    state.user_info = {
      name:cookie.getCookie('name'),
      token:cookie.getCookie('token')
    };
  },
  logout(state){
    state.undiagnosed = false;
    state.diagnosed = false;
    state.transform = false;
    state.statistics = false;
    state.report = false;
    state.leftNav = false;
    state.topHeader=false;
    state.login=true;
    cookie.delCookie('token');
    cookie.delCookie('name');
    cookie.delCookie('hospital_id');
    state.user_info = {
      name:cookie.getCookie('name'),
      token:cookie.getCookie('token')
    };
  },
};

export default new Vuex.Store({
  state,
  mutations
})



